



















































































































































import Vue from "vue";
import Component from "vue-class-component";
import { QuestionModel, QuizResponse, Subject } from "@/model/ResponseModel";
@Component({
  name: "AWS",
  watch: {
    $route(to, from) {
      if (to.query.title !== from.query.title) {
        this.$store.state.aws.quiz = (this.$store.state
          .quizzes as QuizResponse).subjects.filter(
          value => value.name === this.$route.query.title
        )[0] as Subject;
        this.$emit("ROUTE CHANGE", to);
      }
    }
  }
  // Toutes les options de composant sont autorisées ici.
})
export default class AWS extends Vue {
  title = "";
  test = [];
  rightAnswer = 0;
  unanswerOnly = false;
  resultAws = { pos1: 0, pos2: 0, pos3: 0, pos4: 0, top: 0, left: 0 };
  created() {
    this.$store.state.aws.quiz = (this.$store.state
      .quizzes as QuizResponse).subjects.filter(
      value => value.name === this.$route.query.title
    )[0] as Subject;
  }
  mounted() {
    this.resultAws.top = window.innerHeight - window.innerHeight * 0.92;
    this.resultAws.left = window.innerWidth - window.innerWidth * 0.25;

    const resultDoc = document.getElementById("result");
    if (resultDoc != null) {
      resultDoc.style.top = this.resultAws.top + "px";
      //resultDoc.style.left = this.resultAws.left+'px';
      resultDoc.style.left = this.resultAws.left + "px";
    }
    this.dragElement(document.getElementById("result"));

    this.rightAnswer = this.getNumberOfRightAnswer();
    this.$on("ROUTE CHANGE", () => {
      this.rightAnswer = this.getNumberOfRightAnswer();
    });
  }
  showResult(item: QuestionModel) {
    item.showResult = !item.showResult;
  }
  respondQuestion(item: QuestionModel) {
    let valid = true;
    for (const response of item.responses) {
      if (response.isTrue && !response.chose) {
        item.isFalse = true;
        item.isTrue = false;
        valid = false;
      } else if (response.isFalse && response.chose) {
        item.isFalse = true;
        item.isTrue = false;
        valid = false;
      }
    }
    if (valid) {
      item.isTrue = true;
      item.isFalse = false;
    }
    this.rightAnswer = this.getNumberOfRightAnswer();
    localStorage.setItem("quizzes", JSON.stringify(this.$store.state.quizzes));
  }

  getNumberOfRightAnswer() {
    let count = 0;
    for (const question of this.$store.state.aws.quiz
      .questions as QuestionModel[]) {
      if (question.isTrue) {
        count += 1;
      }
    }
    return count;
  }
  resetQuiz() {
    for (const question of this.$store.state.aws.quiz
      .questions as QuestionModel[]) {
      question.isTrue = false;
      question.isFalse = false;
      for (const r of question.responses) {
        r.chose = false;
      }
    }
    this.rightAnswer = this.getNumberOfRightAnswer();
    localStorage.setItem("quizzes", JSON.stringify(this.$store.state.quizzes));
  }

  //BAD CODE ABOVE
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  dragElement(elmnt: any) {
    const closeDragElement = () => {
      this.resultAws.top = elmnt.style.top;
      this.resultAws.left = elmnt.style.left;
      document.onmouseup = null;
      document.onmousemove = null;
    };

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const elementDrag = (e: any) => {
      e = e || window.event;
      e.preventDefault();
      // calculate the new cursor position:
      this.resultAws.pos1 = this.resultAws.pos3 - e.clientX;
      this.resultAws.pos2 = this.resultAws.pos4 - e.clientY;
      this.resultAws.pos3 = e.clientX;
      this.resultAws.pos4 = e.clientY;
      // set the element's new position:
      elmnt.style.top = elmnt.offsetTop - this.resultAws.pos2 + "px";
      elmnt.style.left = elmnt.offsetLeft - this.resultAws.pos1 + "px";
    };

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const dragMouseDown = (e: any) => {
      e = e || window.event;
      e.preventDefault();
      // get the mouse cursor position at startup:
      this.resultAws.pos3 = e.clientX;
      this.resultAws.pos4 = e.clientY;
      document.onmouseup = closeDragElement;
      // call a function whenever the cursor moves:
      document.onmousemove = elementDrag;
    };

    if (document.getElementById(elmnt.id)) {
      // if present, the header is where you move the DIV from:
      const t = document.getElementById(elmnt.id);
      if (t != null) {
        t.onmousedown = dragMouseDown;
      }
    } else {
      // otherwise, move the DIV from anywhere inside the DIV:
      elmnt.onmousedown = dragMouseDown;
    }
  }
}
